.checkout-item{
    width:100%;
    display:flex;
    min-height:100px;
    border-bottom:1px solid darkcyan;
    padding:15px 0;
    font-size:20px;
    align-items:center;

    .image-container{
        width:23%;
        padding-right: 20px;

        img{
            width:100%;
            height:100%;
        }
    }
    .name,.quantity,.price{
        width:20%;
    }
    .quantity{
      display: flex;
      .arrow{
          cursor: pointer;
      }
      .value{
        margin: 0 10px;
      }
    }
    .remove-button{
        padding-left: 12px;
        cursor:pointer;
    }
}