.custom-button{
    min-width:165px;
    width:auto;
    height:50px;
    letter-spacing:0.5px;
    line-height: 50px;
    padding:-0 35px 0 35px;
    font-size:15px;
    background-color:black;
    color:white;
    text-transform: uppercase;
    font-family: 'sans serif';
    font-weight:bolder;
    border:none;
    cursor: pointer;
    display: flex;
    justify-content: center;
&:hover{
    background-color: #18066b;
    color:white;
    border:1px solid black;
}
&.google-sign-in{
    background-color:#4285f4;
    color:white;
    &:hover{
        background-color: #4285f4;
        border:none;
    }
    &.inverted{
        background-color:white;
        color:black;
        border:1px solid black;
         &.hover{
             color:white;
             border:none;
         }
    }
}
}